<template>
  <div class="qa-container">
    <h2 class="title">Q & A</h2>

    <div class="qa-item" v-for="(item, index) in qaList" :key="index">
      <div class="question" @click="toggle(index)">
        <span class="q-label">Q{{ index + 1 }}：</span>
        {{ item.question }}
      </div>

      <div class="answer" v-show="item.show">
        <span class="a-label">A：</span>
        {{ item.answer }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "QuestionAndAnswer",
  data() {
    return {
      qaList: [
        {
          question: "What is the two-dimensional irregular packaging problem?",
          answer:
            "The two-dimensional irregular packing problem refers to placing several irregularly shaped objects in a given container, maximizing space utilization without overlap.",
          show: false,
        },
        {
          question:
            "What are some common application scenarios for this problem?",
          answer:
            "It is commonly used in fields such as sheet metal cutting, clothing pattern making, container loading, and industrial manufacturing.",
          show: false,
        },
        {
          question: "Why is this problem difficult to solve?",
          answer:
            "Because the problem is NP-hard, the solution space is huge, and it involves geometric constraints and combinatorial optimization, the solution is complex.",
          show: false,
        },
      ],
    };
  },
  methods: {
    toggle(index) {
      this.qaList[index].show = !this.qaList[index].show;
    },
  },
};
</script>

