<template>
  <div>
    <div v-if="!jsonData" class="background-text">
      The bin and the polygons are placed here.
    </div>
    <svg :width="svgWidth" :height="svgHeight" style="border: 1px solid #ccc">
      <g :transform="transform">
        <polygon
          :points="formatPoints(container)"
          stroke="black"
          fill="transparent"
          :stroke-width="strokeWidth"
        />
      </g>
      <polygon
        v-for="(points, index) in polygons"
        :key="index"
        :points="formatPoints(points)"
        stroke="black"
        :fill="polygonColors[index % polygonColors.length]"
        :stroke-width="strokeWidth"
        :transform="polygonTransforms[index]"
      ></polygon>
    </svg>
  </div>
</template>

<script>
import axios from "axios";
import { gsap } from "gsap";

export default {
  name: "ContainerAndPolygon",
  props: {
    jsonData: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      svgWidth: 1400,
      svgHeight: 600,
      baseSvgHeight: 600, //  新增：最小高度
      userScale: 1,
      container: [],
      polygonOffsetX: 0,
      polygonOffsetY: 0,
      polygons: [],
      polygonColors: [
        "#409EFF4C",
        // "#4e79a7",
        // "#f28e2b",
        // "#e15759",
        // "#76b7b2",
        // "#59a14f",
        // "#edc948",
        // "#b07aa1",
        // "#ff9da7",
      ],
    };
  },
  watch: {
    jsonData(newVal) {
      if (newVal) {
        // console.log("收到 JSON 数据：", newVal);
        // TODO: 根据 jsonData 渲染内容
        // 1. 更新 container
        this.container = [];
        const bin_x = newVal.container?.x || [];
        const bin_y = newVal.container?.y || [];
        for (let i = 0; i < bin_x.length; i++) {
          this.container.push([bin_x[i], bin_y[i]]);
        }

        // 2. 更新 polygons
        this.polygons = [];
        const items = newVal.items || [];
        for (let i = 0; i < items.length; i++) {
          const xArr = items[i].x || [];
          const yArr = items[i].y || [];
          const poly = [];
          for (let j = 0; j < xArr.length; j++) {
            poly.push([xArr[j], yArr[j]]);
          }
          this.polygons.push(poly);
        }

        // this.$nextTick(() => {
        //   // this.animatePolygons();
        //   console.log("animate");
        // });
      }
    },
    "polygonLayout.maxUsedY": {
      immediate: true,
      handler(val) {
        this.svgHeight = Math.max(this.baseSvgHeight, val + 40);
      },
    },
  },

  computed: {
    polygonLayout() {
      const transforms = [];

      const startX = this.containerRightX + 20;
      let offsetX = startX;
      let offsetY = 0;
      let rowMaxHeight = 0;

      const gapX = 10;
      const gapY = 10;

      let maxUsedY = 0;

      this.polygons.forEach((polygon) => {
        const { width, height, minX, minY } = this.getPolygonBounds(polygon);
        const polyWidth = width * this.scale;
        const polyHeight = height * this.scale;

        if (offsetX + polyWidth > this.svgWidth) {
          offsetX = startX;
          offsetY += rowMaxHeight + gapY;
          rowMaxHeight = 0;
        }

        transforms.push(
          `translate(${offsetX - minX * this.scale}, ${
            offsetY - minY * this.scale
          }) scale(${this.scale})`
        );

        offsetX += polyWidth + gapX;
        rowMaxHeight = Math.max(rowMaxHeight, polyHeight);
        maxUsedY = Math.max(maxUsedY, offsetY + polyHeight);
      });

      return {
        transforms,
        maxUsedY,
      };
    },

    polygonTransforms1() {
      return this.polygonLayout.transforms;
    },
    containerRightX() {
      const { width } = this.bounds;
      const scale = this.scale;

      // 和 transform() 中 offsetX 保持一致
      const containerOffsetX = (width - this.bounds.minX) * scale * 0.05;

      return containerOffsetX + width * scale;
    },
    bounds() {
      let minX = Infinity,
        minY = Infinity,
        maxX = -Infinity,
        maxY = -Infinity;
      this.container.forEach(([x, y]) => {
        if (x < minX) minX = x;
        if (y < minY) minY = y;
        if (x > maxX) maxX = x;
        if (y > maxY) maxY = y;
      });
      return {
        minX,
        minY,
        maxX,
        maxY,
        width: maxX - minX,
        height: maxY - minY,
      };
    },
    scale() {
      return Math.min(
        this.svgWidth / (this.bounds.width * 3.05),
        this.svgHeight / (this.bounds.height * 3.05)
      );
    },
    transform() {
      const { minX, minY, width, height } = this.bounds;
      const scale = this.scale;
      const offsetX = (width - minX) * scale * 0.05;
      const offsetY = (this.svgHeight - height * scale) / 2 - minY * scale;
      return `translate(${offsetX || 0},${offsetY || 0}) scale(${scale || 1})`;
    },
    polygonTransforms() {
      const transforms = [];
      const startX = this.containerRightX + 20; // ← 右侧起点 + 间距
      const startY = 0;
      let offsetX = startX;
      let offsetY = startY;
      let rowMaxHeight = 0;

      const gapX = 10; // 横向间距（可调）
      const gapY = 10; // 行间距（强烈建议）

      this.polygons.forEach((polygon) => {
        const { width, height, minX, minY } = this.getPolygonBounds(polygon);
        const polyWidth = width * this.scale;
        const polyHeight = height * this.scale;

        // 如果当前行放不下，换行
        if (offsetX + polyWidth > this.svgWidth) {
          offsetX = startX;
          offsetY += rowMaxHeight + gapY;
          rowMaxHeight = 0;
        }

        // 记录 transform
        transforms.push(
          `translate(${offsetX - minX * this.scale}, ${
            offsetY - minY * this.scale
          }) scale(${this.scale})`
        );

        // 更新游标
        offsetX += polyWidth + gapX;
        rowMaxHeight = Math.max(rowMaxHeight, polyHeight);
      });

      return transforms;
    },
    strokeWidth() {
      // 设置边框为 container 最大边长的 0.2%
      const { width, height } = this.bounds;
      const maxLen = Math.max(width, height);
      return maxLen * 0.005;
    },
  },
  mounted() {
    // this.readFile();
  },
  methods: {
    animatePolygons() {
      // 选择所有右侧多边形（不是 container polygon）
      const polygonEls = this.$el.querySelectorAll("polygon");

      gsap.fromTo(
        polygonEls,
        { scale: 0, transformOrigin: "50% 50%" },
        {
          scale: 1,
          duration: 1,
          ease: "elastic.out(1, 0.5)",
          // delay: index * 0.1,
        }
      );
    },
    async readFile() {
      axios.get("/data/atris1121.cgshop2024_instance.json").then((res) => {
        // console.log(res.data);
        this.container = [];
        this.polygons = [];
        var bin_x = res.data.container.x;
        var bin_y = res.data.container.y;
        for (let i = 0; i < bin_x.length; i++) {
          let point = [bin_x[i], bin_y[i]];
          this.container.push(point);
        }
        // console.log(this.container);

        this.polygons = [];
        var items = res.data.items;
        for (let i = 0; i < items.length; i++) {
          var polygon = [];
          var x = items[i].x;
          var y = items[i].y;
          for (let j = 0; j < x.length; j++) {
            let point = [x[j], y[j]];
            polygon.push(point);
          }
          this.polygons.push(polygon);
        }
        // console.log(this.polygons);
      });
    },
    // 将点数组转换成 SVG polygon 可识别的字符串
    formatPoints(points) {
      return points.map((p) => p.join(",")).join(" ");
    },
    getPolygonBounds(polygon) {
      let minX = Infinity,
        minY = Infinity,
        maxX = -Infinity,
        maxY = -Infinity;
      polygon.forEach(([x, y]) => {
        minX = Math.min(minX, x);
        minY = Math.min(minY, y);
        maxX = Math.max(maxX, x);
        maxY = Math.max(maxY, y);
      });
      return {
        minX,
        minY,
        width: maxX - minX,
        height: maxY - minY,
      };
    },
  },
};
</script>

<style>
.polygon-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.background-text {
  position: absolute; /* 放在页面上层，绝对定位 */
  top: 40%; /* 垂直居中 */
  left: 50%; /* 水平居中 */
  transform: translate(-50%, -50%); /* 精准居中 */

  font-family: "Poppins", "Helvetica Neue", Arial, sans-serif; /* 时尚字体 */
  font-size: 30px; /* 响应式较小字体 */

  font-weight: 900; /* 超粗体 */
  color: rgba(200, 200, 200, 0.2); /* 灰色，半透明，像背景 */
  letter-spacing: 5px; /* 字母间距增加视觉感 */
  user-select: none; /* 不可选中 */
  pointer-events: none; /* 不影响鼠标操作 */
  white-space: nowrap; /* 不换行 */
  z-index: 0; /* 确保在内容后面，可调整 */
}
</style>

