<template>
  <div class="containers-wrapper">
    <!-- 加载动画 -->
    <div v-if="loading" class="loading-wrapper">
      <div class="spinner"></div>
      <div class="loading-text">
        <i class="el-icon-loading"></i>calculating...
      </div>
    </div>
    <div
      v-for="(container, cIndex) in containers"
      :key="cIndex"
      class="container-item"
    >
      <svg
        :width="container.width"
        :height="container.height"
        :viewBox="`0 0 ${container.width} ${container.height}`"
      >
        <!-- container 边界 -->
        <polygon
          :points="rectToPoints(container.width, container.height)"
          fill="none"
          stroke="#409eff"
          stroke-width="2"
        />

        <!-- polygons -->
        <polygon
          v-for="(poly, index) in container.polygons"
          :key="index"
          :points="xyArrayToPoints(poly.x, poly.y)"
          fill="rgba(64,158,255,0.3)"
          stroke="#333"
          stroke-width="1"
        />
      </svg>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "PolygonCanvas",
  data() {
    return {
      containers: [], // 多 container 数组
      loading: false,
    };
  },
  props: {
    inputData: {
      type: Object,
      default: null,
    },
    visible: {
      type: Boolean,
    },
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.loading = true;
        this.containers = [];
        axios
          .post("http://localhost:8090/polygon/input", this.inputData)
          .then((res) => {
            // console.log(res.data);
            this.containers = res.data;
            this.$message.success("Calculation completed");
            this.loading = false;
          })
          .catch((err) => {
            this.$message.error(err.code);
          });
      }
    },
  },
  mounted() {
    // console.log(this.jsonData);
    // this.getData();
    if (this.inputData) {
      this.loading = true;
      this.containers = [];
      axios
        .post("http://localhost:8090/polygon/input", this.inputData)
        .then((res) => {
          this.containers = res.data;
          this.$message.success("Calculation completed");
          this.loading = false;
        })
        .catch((err) => {
          this.$message.error(err.code);
        });
    }
  },
  methods: {
    getData() {
      this.loading = true;
      axios.get("http://localhost:8090/test/results").then((res) => {
        // console.log(res);
        this.containers = []; // 防止重复请求叠加
        // 假设 res.data 是数组，每个元素是一个 container 的数据
        res.data[0].forEach((containerData) => {
          const container = {
            width: containerData.xmax,
            height: containerData.ymax,
            polygons: [],
          };

          containerData.pzasInside.forEach((polygon) => {
            container.polygons.push({
              x: polygon.coordX,
              y: polygon.coordY,
            });
          });

          this.containers.push(container);
        });
        this.loading = false;
        // console.log(this.containers);
      });
    },

    // container 矩形 → polygon points
    rectToPoints(w, h) {
      return `0,0 ${w},0 ${w},${h} 0,${h}`;
    },

    // x[] + y[] → "x,y x,y ..."
    xyArrayToPoints(xArr, yArr) {
      if (!xArr || !yArr || xArr.length !== yArr.length) {
        return "";
      }
      return xArr.map((x, i) => `${x},${yArr[i]}`).join(" ");
    },
  },
};
</script>

<style scoped>
/* 外层容器 flex 布局，一行尽量放 2~3 个 container */
.containers-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  justify-content: left;
}

.container-item {
  flex: 1 1 calc(33% - 16px); /* 一行最多放 3 个 */
  max-width: 400px;
  min-width: 250px;
  background: #fff;
  border: 1px solid #ddd;
  padding: 4px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

svg {
  width: 100%;
  height: auto;
  display: block;
}
.loading-text {
  display: inline-flex; /* 图标和文字一行 */
  align-items: center; /* 垂直居中 */
  gap: 8px; /* 图标和文字间距 */
  font-size: 24px; /* 字体变大 */
  font-weight: 500; /* 可选，加粗一点 */
}
</style>
